<template>
  <div id="app" class="app">
    <!-- 1. PC -->
    <router-view v-if="!$store.state.isMobile" />

    <!-- 2.MOBILE -->
    <div v-if="$store.state.isMobile" class="noMobile">
      <p>此网站暂不支持移动端查看</p>
      <br />
      <p>请使用PC端浏览器访问此网站</p>
    </div>
  </div>
</template>

<script>
export default {
  created() {},
  methods: {
    getScreen() {
      let clientWidth =
        document.documentElement.clientWidth || document.body.clientHeight;

      if (clientWidth < 980) {
        //当屏幕小于980时，设置Vuex里的数据为true
        this.$store.commit("SET_MOBLIE", true);
      } else {
        //反之，设置Vuex里的数据为false
        this.$store.commit("SET_MOBLIE", false);
      }
    },
    setMobile() {
      // 页面已加载完就需要初始化一次状态，后续 再监听 页面宽度变更事件。
      this.getScreen();

      //监听屏幕
      addEventListener("resize", () => {
        this.getScreen();
      });
    },
  },
  mounted() {
    // 初始化化屏幕尺寸
    this.setMobile();

    // 日志输出
    console.log(
      ".####....##......##....###....##....##.########....########..#######......######..########.########....##....##..#######..##.....##\n..##.....##..##..##...##.##...###...##....##..........##....##.....##....##....##.##.......##...........##..##..##.....##.##.....##\n..##.....##..##..##..##...##..####..##....##..........##....##.....##....##.......##.......##............####...##.....##.##.....##\n..##.....##..##..##.##.....##.##.##.##....##..........##....##.....##.....######..######...######.........##....##.....##.##.....##\n..##.....##..##..##.#########.##..####....##..........##....##.....##..........##.##.......##.............##....##.....##.##.....##\n..##.....##..##..##.##.....##.##...###....##..........##....##.....##....##....##.##.......##.............##....##.....##.##.....## \n.####.....###..###..##.....##.##....##....##..........##.....#######......######..########.########.......##.....#######...#######.      by  lazy_tomato"
    );
  },
};
</script>

