import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [

  {
    path: '/',
    name: 'Home',
    component: () => import('@/views/Home/index.vue')
  },
  {
    path: '/Keyboard',
    name: 'Keyboard',
    component: () => import('@/views/Keyboard/index.vue')
  },
  {
    path: '/CommemorationDay',
    name: 'CommemorationDay',
    component: () => import('@/views/CommemorationDay/index.vue')
  },
  {
    path: '/AboutMe',
    name: 'AboutMe',
    component: () => import('@/views/AboutMe/index.vue')
  },
  {
    path: '/404',
    name: '404',
    component: () => import('@/views/404'),
  },
  {
    path: '*',
    redirect: '/404',
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router